p {
  font-family: $font-family;
  font-size: 1rem;
  line-height: 1.7;
  color: $text-color;
  margin-bottom: 1.2em;
}

p strong {
  font-weight: bold;
}

p em {
  font-style: italic;
}

p a {
  color: $primary-color;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }
}
